
.bsc-breadcrumb {
  font-size: 14px;
}
.bsc-breadcrumb a {
  -webkit-transition: color 0.2s ease-in-out;
  transition: color 0.2s ease-in-out;
}
.bsc-breadcrumb a:hover {
  color: #5cadff;
}
.bsc-breadcrumb > span:last-child > a {
  color: #99a9bf;
  cursor: default;
}
.bsc-breadcrumb > span:last-child .bsc-breadcrumb-item-separator {
  display: none;
}
.bsc-breadcrumb-item-separator {
  margin: 0 8px;
  color: #d7dde4;
}
.bsc-breadcrumb-item-link > .ivu-icon + span {
  margin-left: 4px;
}



